import styled from "styled-components";
import React from "react";

export const CheckBoxWrapper = styled.div`
  position: relative;
`;
export const CheckBoxLabel = styled.label`
  position: absolute;
  top: 0;
  left: 0;
  width: 42px;
  margin: 22px;
  height: 24px;
  border-radius: 15px;
  background: #bebebe;
  cursor: pointer;
  &::after {
    content: "";
    display: block;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    margin: 3px;
    background: #ffffff;
    box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.2);
    transition: 0.2s;
  }
`;
export const CheckBox = styled.input`
  opacity: 0;
  z-index: 1;
  border-radius: 15px;
  width: 42px;
  height: 52px;
  &:checked + ${CheckBoxLabel} {
    background: #670fdf;
    &::after {
      content: "";
      display: block;
      border-radius: 50%;
      width: 18px;
      height: 18px;
      margin-left: 21px;
      transition: 0.2s;
    }
  }
`;

function Toggle(props) {
  const { name, toggle, setToggle } = props;

  const handleToggle = () => {
    setToggle(!toggle);
  };

  return (
    <CheckBoxWrapper>
      <CheckBox
        id={name}
        name={name}
        type="checkbox"
        checked={toggle}
        onChange={handleToggle}
      />
      <CheckBoxLabel htmlFor={name} />
    </CheckBoxWrapper>
  );
}

export default Toggle;
